@import url("../fonts/roboto/stylesheet.css");

$font: SF, Pro, Display, Arial, Helvetica, sans-serif;

body {
  margin: 1em;
}
.link {
  text-decoration: none;
  font-family: $font;
}
.header {
  display: flex;
  padding-top: 2.5em;
  text-align: start;
  align-items: flex-end;
  .header__img{
    height: 1.4em;
    width: 1.2em;
    padding-right: 5.4375em;
  }
  .header__a_directions-top-level {
    text-align: center;
    color: black;
    font-size: 0.875em;
  }
}
.phone-list {
  display: flex;
  flex-direction: column;
  overflow: auto;
  white-space: nowrap;
  .phone-list__b {
    font-family: $font;
    padding-top: 1.9em;
    padding-bottom: 1.1em;
    font-size: 1.5em;
  }
  .phone-list__item-list {
    display: flex;
    overflow-x: scroll;
    flex-direction: row;
    scroll-behavior: smooth;
    height: 5.1875em;
    .phone-list__item {
      display: flex;
      align-items: center;
      justify-content: space-between;
      background: #59A4F2;
      height: 5.1875em;
      width: 14.5em;
      border-radius: 0.90625em;
      margin-right: 1.25em;
      padding-left: 1.25em;
      padding-right: 1.25em;
      .phone-list__wrap-img {
        .phone-list__img {
        }
      }
      .phone-list__wrap-text {
        word-break: break-all;
        text-align: start;
        padding-left: 1em;
        .phone-list__a {
          .phone-list__p {
            color: white;
            font-size: 1em;
            word-break: break-all;
          }
        }
      }
    }
    .phone-list__add-item{
      height: 5.17em;
      width: 5.15em;
      border: 1px dashed #F1564A;
      box-sizing: border-box;
      border-radius: 10px;
      text-align: center;
      justify-content: center;
      padding: 2.062em;
      margin-right: 1.25em;
    }
  }
}